<template>
<main class="main">
  <AppSteps :step="2"/>
  <div class="add-product">
    <h2 class="add-product__title">Add a product</h2>
    <AppImgSelect class="add-product__region-select" :optionList="regionList" :placeholder="'Region'" @select="selectRegion" v-if="!selectedRegion"/>
    <div class="selected-items" v-if="selectedRegion">
        <div class="selected-items__title" >
            Region
        </div>
        <AppSelectedItems :itemList="[selectedRegion]"  @cancel="cancelRegion"/>
    </div>
    <div class="selected-items" v-if="selectedRegion">
        <div class="selected-items__title">
            Server
        </div>
        <AppCustomSelect class="add-product__region-select" 
        :optionList="serverList" 
        :type="'search'" 
        :placeholder="'Enter a server'"
        v-if="!selectedServer"
        @select="selectServer"/>
        <AppSelectedItems :itemList="[selectedServer]"  @cancel="cancelServer" v-if="selectedServer"/>
    </div>
</div>
<ul class="main__step-btns">
    <li><button class="step-back-btn" @click="$router.push('/')">Step 1</button></li>
    <li><button class="step-next-btn" v-if="selectedServer">next</button></li>
</ul>
</main>
  
</template>

<script>
import AppSteps from '@/components/AppSteps.vue';
import AppImgSelect from '@/components/AppImgSelect.vue'
import AppSelectCategory from '@/components/AppSelectCategory.vue';
import AppSelectedItems from '@/components/AppSelectedItems.vue';
import AppCustomSelect from '@/components/AppCustomSelect.vue'


export default {
    components: {
        AppSteps,
        AppImgSelect,
        AppSelectCategory,
        AppSelectedItems,
        AppCustomSelect
    },
    data(){
      return {
        selectedRegion:'',
        selectedServer:'',
        regionList:[
          { text:'SELECT ALL'},
          {img: '/img/icons/region.png', text:'EU West'},
          {img: '/img/icons/region.png', text:'EU West'},
          {img: '/img/icons/region.png', text:'EU West'},
          {img: '/img/icons/region.png', text:'EU West'},
        ],
        serverList:['Amnennar TBC - Alliance', 'Amnennar TBC - Horde'],
       }
    },
    methods:{
      selectRegion(option){
        this.selectedRegion = option.text
      },
      cancelRegion(){
        this.selectedRegion = ''
      },
      selectServer(server){
        this.selectedServer = server
      },
      cancelServer(){
        this.selectedServer = ''
      },
    },

}
</script>

